:root {
    --gold: #cc9842
}

.WeddingAdvert {
    width: 100%;
    height: 90%;
    background-image: url('../image/jenny-bg.png');
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: var(--gold);
}

.WeddingAdvert .span {
    width: 45%;
    text-align: center;
}

.container-input {
    position: relative;
    width: 45%;
    margin-top: 2rem;
}

.input {
    height: 90px;
    width: 98%;
    border-radius: 50px;
    border: none;
    padding-left: 1rem;
    font-size: 16px;
}

.button {
    cursor: pointer;
    height: 80px;
    width: 22%;
    border-radius: 50px;
    border: none;
    position: absolute;
    top: 5px;
    right: 0px;
    font-size: 16px;
    color: #fff;
    background-color: #bd8220d9;
    background-image: linear-gradient(to right, #edc57f, #bd8220d9, #bd8220d9, #edc57f);
    background-size: 300% 100%;
    transition: all .3s ease-in-out;
}

.button:hover {
    transition: all .3s ease-in-out;
    color: #fff;
    background-position: 100% 0;
}

.container-footer {
    background-color: #fff;
    height: 80px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 0;
    position: absolute;
    border-top-left-radius: 100px;
    border-top-right-radius: 100px;
}

.grid {
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100% / 2);
}

.grid span {
    margin: 0 1.5rem;
    font-weight: bold;
    transition: .3s ease-in-out;
}

.grid span:hover {
    cursor: pointer;
    color: #000;
    transition: .3s ease-in-out;
}

.icon {
    margin: 0 1rem;
    transition: .3s ease-in-out;
}

.icon:hover {
    cursor: pointer;
    color: #000;
    transition: .3s ease-in-out;
}

@media only screen and (max-width: 460px) {
    .WeddingAdvert .span{
        width: 80%;
    }
    .span {
        width: 80%;
    }

    .container-input {
        width: 90%;
    }

    .input {
        height: 70px;
    }

    .button {
        width: 30%;
        height: 60px;
    }

    ::placeholder {
        font-size: 14px;
    }
}

@media only screen and (max-width: 400px) {
    .input {
        height: 45px;
    }

    .button {
        width: 25%;
        height: 35px;
        font-size: 12px;
    }

    ::placeholder {
        font-size: 12px;
    }

    .container-footer {
        height: 50px;
    }

    .grid {
        font-size: 14px;
    }
}