.WeddingCount {
    position: relative;
    height: 600px;
    width: 100%;
    background-color: #fff;
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #cc9842;
}

.container {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container-time {
    width: 150px;
    height: 150px;
    border: 5px solid #cc9842;
    border-radius: 8px;
    margin: 0 2rem;
}

.box {
    width: 150px;
    height: 150px;
    background-color: #fff;
    border-radius: 8px;
    font-size: 22px;
    transform: translate3d(15px, -10px, 0);
    box-shadow: -2px 2px 5px 1px #dbdbdb;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.f-number {
    font-size: 38px;
    font-weight: bold;
}

.show-counter {
    display: flex;
    text-align: center;
}

.countdown {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

@media only screen and (max-width: 460px) {
    .f-txt-countdown {
        text-align: center;
        padding: 0 2rem;
    }
    .container-time {
        width: 65px;
        height: 85px;
        margin: 0 .8rem;
    }

    .box {
        width: 65px;
        height: 85px;
        transform: translate3d(10px, -10px, 0);
    }

    .f-number {
        font-size: 30px;
    }

    .f-txt {
        font-size: 18px;
    }
}

@media only screen and (max-width: 400px) {
    .WeddingCount {
        height: 800px;
    }
    .container {
        margin-top: 3rem;
    }

    .show-counter {
        flex-direction: column;
    }

    .container-time {
        margin: .5rem;
    }
}